import React from 'react';
import '../home.css';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {date: new Date()};
    }
    componentDidMount() {
        document.body.style.overflow = "hidden";
        this.timerID = setInterval(
            () => this.tick(),
            1000
        );
    }
    componentWillUnmount() {
        document.body.style.overflow = "auto";
        clearInterval(this.timerID);
    }

    tick() {
        this.setState({date: new Date()});
    }

    handleClick(id) {
        if(id === "whut") {
            alert("Ouch, you've clicked at me. :(");
            return;
        }

        this.props.history.push(id);
    }
    render() {
        return(
            <div>
                <div className="topClock">Clock: {this.state.date.toLocaleTimeString()}</div>
                <header className="masthead d-flex">
                    <div className="container text-center my-auto">
                        <h1 className="mb-1 homeText">ARFAN</h1>
                        <h3 className="mb-5">
                            <em>A man with a big dreams</em>
                        </h3>

                        <div className="btn-group btn-group-lg" role="group" aria-label="Navigation">
                            <button type="button" className="btn btn-secondary" onClick={this.handleClick.bind(this, '/biography')}>Biography</button>
                            <button type="button" className="btn btn-secondary" onClick={this.handleClick.bind(this, '/projects')}>Projects</button>
                            <button type="button" className="btn btn-secondary" onClick={this.handleClick.bind(this, 'whut')}>(?)</button>
                        </div>

                    </div>
                    <div className="overlay"></div>
                </header>
            </div>
        );
    }
}

export default Home;