import {BrowserRouter as Router, Link, Route, Switch, useLocation} from "react-router-dom";
import React, {Component, ReactDOM} from "react";
import * as Pages from "./pages";

const defaultTitle = "Arfan | Story";

const routers = [
    {
        path: "/",
        title: "Arfan | Home",
        exact: true,
        component: Pages.Home
    },
    {
        path: "/biography",
        title: "Arfan | Biography",
        component: Pages.Biography
    },
    {
        path: "/projects",
        component: Pages.Projects,
        routes: [
            {
                path: "/projects/ktmb",
                component: KTMB
            }
        ]
    },
    {
        path: "/:404",
        title: "WTF",
        component: NoMatch
    }
];

class MainNav extends Component {
    handleTitle = () => {
        const location = window.location.pathname;
        let obj = routers.filter((e) => e.path === location)[0];

        if(obj !== undefined) {
            if(obj.title !== undefined)
                document.title = obj.title;
            else
                document.title = defaultTitle;
        } else {
            document.title = "404 Not Found";
        }
    };
    render() {
        return (
            <Router>
                {this.handleTitle()}
                <Switch>
                    {routers.map((route, i) => (
                        <RouteWithSubRoutes key={i} {...route} />
                    ))}
                </Switch>
            </Router>
        );
    }
}

function RouteWithSubRoutes(route) {
    return (
        <Route
            path={route.path}
            render={props => (
                // pass the sub-routes down to keep nesting
                <route.component {...props} routes={route.routes} />
            )}
        />
    );
}

function KTMB() {
    return <div></div>;
}

function NoMatch() {
    let location = useLocation();

    return (
        <div className="container">
            <div className="card">
                <div className="card-header">
                    <h3>
                        Error 404: No match for <code>{location.pathname}</code>
                    </h3>
                </div>
                <div className="card-body">
                    The page that you currently looking for is not exists :(
                </div>
            </div>
        </div>
    );
}

export default MainNav;